<template>
	<div>
		<div class="table-responsive">
      <table class="table table-striped">
        <thead>
          <tr>
            <th>用户名</th>
            <th>电话号码</th>
            <th>注册时间</th>
            <th>推荐票</th>
            <th>钻石票</th>
            <th>猫币余额</th>
            <th>是否禁用</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody v-for="userInfoItem in userInfo">
          <tr>
            <td>{{userInfoItem.userName}}</td>
            <td>{{userInfoItem.phone}}</td>
            <td>{{userInfoItem.registerTime}}</td>
            <td>{{userInfoItem.recommendTicket}}</td>
            <td>{{userInfoItem.diamondTicket}}</td>
            <td>{{userInfoItem.balance}}</td>
            <td v-if="userInfoItem.isForbidden">用户禁用</td>
            <td v-else>用户可用</td>
            <td><a  href="#modal" v-on:click="editUser(userInfoItem)">编辑</a></td>
          </tr>
        </tbody>
      </table>
	  </div>

	  <div class="remodal text-left" data-remodal-id="modal">
      <button data-remodal-action="close" class="remodal-close"></button>
      <div style="border-bottom: solid 1px #E6E6E6; margin-bottom: 20px;">
          <h3>修改用户信息</h3>                
      </div>
      <div class="row">
          <div class="col-sm-6">
              <label>用户名</label>
              <input v-model="editUserName" type="text" class="form-control">                                
          </div>
      </div>
      <div class="row" style="margin-top: 15px;">
          <div class="col-sm-6">
              <label>手机号</label>
              <input v-model="editPhone" type="text" class="form-control">                              
          </div>
      </div>
      <div class="row" style="margin-top: 15px;">
          <div class="col-sm-6">
              <div class="form-group" style="float: left;">
                  <label>是否禁用</label>
                  <input type="checkbox" class="form-control">
              </div>                                
          </div>
      </div>
      <div class="row">
          <div class="form-group col-sm-4">
              <div v-on:click="saveUserInfo" class="btn btn-primary">
                  保存更改
              </div>
          </div>
      </div>
	  </div>

	  <nav class="text-right" style="margin-bottom: 50px;">
      <span>页数:{{numPage}}/{{pageNumber}}</span>
      <button v-on:click="turnPage(numPage-1)" type="button" class="btn btn-primary" style="margin-left: 10px;">上一页</button>
      <button v-on:click="turnPage(numPage+1)" type="button" class="btn btn-primary" style="margin-left: 5px;">下一页</button>
	  </nav>
	</div>
</template>

<script>
	export default {
    data(){
    	return {
	      numPage: 1,
	      pageNumber: "",

	      editId: "",
	      editUserName: "",
	      editPhone: "",
	      editUserType: "",
	      editPassword: "",

	      userInfo: []
	    }
    },
    created: 
    	function(){
    		//页面加载时请求
    		const _this = this
    		$.ajax({
	        url: "http://www.3roo.cn/ShowUserListAPIView/",
	        async: false,
	        type: "GET",
	        data: {numPage: _this.numPage},
	        dataType: 'json',
	        success: function(data) {
	        	_this.clone(data)
	        },
	        error: function() {
	          alert("加载失败");
	        }
		    });
    	},
    methods: {
	    turnPage: function (numPage) {
        //书籍翻页
        const _this = this
        if(numPage<=_this.pageNumber&&numPage>0)
        {
          _this.numPage = numPage
          $.get('http://www.3roo.cn/BookListAPIView/', {
            numPage: numPage
          }, function(data) {
              //data = $.parseJSON(data);
            _this.clone(data);
          });
        }
      },
      clone: function(obj) {
        this.pageNumber = obj.pageNumber
        this.userInfo = obj.userInfo
      },
	    editUser: function(user){
        this.EditState = 1
        this.editId = user.id;
        this.editUserName = user.userName;
        this.editPhone = user.phone;
	    },
	    saveUserInfo: function(){
        $.ajax({
				  beforeSend: csrfTokenHeader,
          url: "http://www.3roo.cn/EditUserAPIView/",
          async: false,
          type: "POST",
          data: {
              userId: this.editId,
              userName: this.editUserName,
              userPhone: this.editPhone,
              userPassword: this.editPassword,
              userType: 1
          },
          success: function(data) {
            window.location.href = "http://www.3roo.cns/userManager/";
          },
          error: function() {
            alert("创建失败");
          }
        })
	    }
    }
	}
</script>


